.footer{
    background-color: #eee;
    padding-top: 20px;
    margin-top: 80px;
}
.footer-part{
    text-align: left;
    text-decoration: none;
    list-style: none;
    padding: 20px;
}
.footer-heading{
font-size: 30px;
}
.Privacy{
    margin-top: 20px;
}
.Privacy a{
    text-decoration: none;
    font-weight: 500;
    font-size: 18px;
}
.footer-heading span{
border-bottom: 1.5px solid #666;
}
.pages-link a{
    text-decoration: none;
    color: #08f;
    font-size: 16px;
}
.copy-text{
    position: absolute;
    background-color: rgb(143, 143, 143);
    width: 100%;
    padding-top: 10px;

}
.socail-icons-footer{
    display: flex;
    align-items: center;
    margin-top: 20px;
}
.socail-icons-footer li{
    margin-right: 15px;
}
.socail-icons-footer li i{
    font-size: 28px;
    color: #222;
}
.socail-icons-footer li i:first-child:hover{
    font-size: 28px;
    color: rgb(4, 70, 128)
}
