.item-card{
    position: relative;
}
.remove-from-cart-btn{
    position: absolute;
    width: 30px;
    height: 30px;
    right: 5px;
    top: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.update-btn{
    width: 40px;
    margin-right: 10px;
    background-color: #f50;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    height: 30px;
    line-height: 0;
    border: none;
    border-radius: 5px;
}
.empty-basket{
    width: 10%;
}
.cuponfield{
    border-radius: 10px;
    border: none;
    background-color: #f50;
    outline: none;
    padding: 5px;
    font-size: 20px;
    font-weight: 700;
    width: 200px;
    text-align: center;
}
.user-input{
    padding: 2px 5px;
    border: none;
    border-bottom: 1px dashed rgb(118, 94, 82);
    outline: none;
}
.couponMsgYes{
    color: rgb(4, 177, 1);
}
.couponMsgNo{
    color: rgb(243, 39, 39);
}
.apply-coupon-btn{
    padding: 6px;
    border-radius: 10px;
    background-color: #fff;
    outline: none;
    border: 2px solid #222;
}
.apply-coupon-btn:hover{
    background-color: #f50;

}