/* ///Add size table and form css// */
/* ===================================================== */
.progressBar{
    max-width:300px;
    position: relative;
    left: 50.7%;
    right: 50%;
    transform: translate(-50%, +50%);
    
}

.input_addProduct_container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 50px;
}
.input_row{
    margin-top: 10px;
    width: 320px;
    text-align: start;
    font-size: 16px;
    font-weight: 500;
}
.product-input-field{
    padding: 4px;
    border: 1px solid #33333388;
    border-radius: 10px;
    margin-left: 10px;
    outline: none;
}

.content_box{
   width: 100%;
   text-align: center;
   margin: 0px 10px;
   display: flex;
   justify-content: center;
}

.quantity_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
}
.custome-table-size{
   max-width:600px;
}

.form-quantiy-field{
    padding: 5px;
    border-radius: 10px;
    outline: none;
    border: 1px solid #33333388;
    text-align: center;
    width: 100px;
}
.quantity_td{
    padding: 0;
    margin: 0;
}