.table-container{
    overflow-x: scroll;
}
.status-table{
    min-width: 700px;
    margin-left: 20px;
    margin-top: 20px;
    margin-right: 20px;
   
}
.status-btn button{
    border: none;
    margin: 2px;
    padding: 3px 8px;
    background-color: rgba(240, 244, 6, 0.884);
    border-radius: 10px;
    font-weight: 500;
}
.status-btn button:hover{
    background-color: rgba(0, 0, 0, 0.884);
    color: #fff;
}