
.login_page{
    background-color: rgba(174, 168, 168, 0.311);
}
.login_form{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.login_form input{
   width: 300px;
   padding: 5px;
   margin: 10px 0;
}
.login_headline{
position:relative;
top: -20px;
}
.login_headline::after{
position:absolute;
content: "";
height: 2px;
width: 120%;
background-color: blue;
border-radius: 10px;
left: -13px;
bottom: -10px;

}