.coupon-wraper{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: left;
    text-align: start;
    margin: 20px;
}
.coupon-content input{
    border: 1px solid #777;
    padding: 3px;
    border-radius: 20px;
    font-size: 16px;
    font-weight: 600;
    padding-left: 10px;
    outline: none;
    width: 150px;
}
.coupon-content span{
    font-size: 16px;
    font-weight: 600;
}
.coupon-active-btn{
    padding: 5px;
    width: 200px;
    border-radius: 20px;
    background-color: #e0d90a;
    font-size: 17px;
    font-weight: 600;
    border: 1px solid #333;
}
.coupon-active-btn:hover{
    background-color: #0daf08;
}

.coupon-table-wraper{
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   flex-direction: column;
}
.coupon-table{
width:auto;
overflow-x: scroll !important;
}
.coupon-table table td{
    padding: 5px;
    font-size: 16px;
    font-weight: 500;
}
.cpn-dlt-btn{
    border: none;
    color: #fb2723;
    font-size: 20px;
    padding: 0 5px 0 5px;
}