
.manage-product-img{
    display: flex;
    justify-content: flex-start;
    max-width: 150px;
    margin: 10px;
}


.manage_info{
    text-align: start;
    padding: 15px;
}
.manage-btn{
    width: 80px;
    padding: 5px;
    font-weight: 500;
    border: 1px solid #555;
    border-radius: 10px;
    margin: 10px 10px 10px 0;
}
.manage-btn a{
   text-decoration: none;
   color:#fff
}

.searchs-field{
    padding: 5px;
    border-radius: 20px;
    width: 300px;
    border: 1px solid #555;
    padding-left: 20px;
}
.searchbtn{
    padding: 5px;
    border-radius: 0 20px 20px 0;
    border: 1px solid #555;
    background-color: #faee11;
    position: relative;
    left: -59px;
    font-weight: 500;

}